<template>
	<div id="deposit-order-detail">
		<public-header title="订单详情"></public-header>
		<div class="content">
			<div class="order-status bg-default margin-10 padding-10-t text-center">
				<div>
					<span class="font-18 font-bold" @click="onTogglePopup">订单{{order.status_cn.text}}</span><i class="icon icon-right c-gray"></i>
				</div>
				<div class="c-gray margin-15-tb font-14" v-if="order.last_log">{{order.last_log.note}}</div>
				<div class="order-btn margin-10-b margin-15-t" v-if="order.status == 1">
					<template v-if="order.status == 1">
						<div class="btn" @click="onUpdateOrderStatus(order.id, 'refund_apply')">申请押金退款</div>
					</template>
				</div>
			</div>
			<template v-if="order.status == 2">
				<div class="margin-10-l margin-10-r">
					<div class="pay-code">
						<div>
							<div id="qrcode" class="qrcode"></div>
							<p class="text-center font-12 margin-10-t">退款状态: {{order.refund.text}}</p>
						</div>
						<p class="code-text">请配送员扫描二维码即可退押金</p>
						<div class="wrap-button">
							<van-button type="primary" size="small" @click="onQueryRefundStatus()">查询退款状态</van-button>
						</div>
					</div>
				</div>
			</template>
			<div class="bg-default margin-10 padding-10 font-14" v-if="order.refund_status > 0">
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">退款编号</van-col>
					<van-col span="17">{{order.refund_out_no}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">退款申请时间</van-col>
					<van-col span="17">{{order.refund_apply_time_cn}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">退款状态</van-col>
					<van-col span="17">{{order.refund.text}}</van-col>
				</van-row>
				<template v-if="order.refund_status == 3">
					<van-row class="margin-10-tb">
						<van-col span="7" class="c-gray">退款到账时间</van-col>
						<van-col span="17">{{order.refund_success_time_cn}}</van-col>
					</van-row>
				</template>
			</div>
			<div class="bg-default margin-10 padding-10 font-14">
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">订单编号</van-col>
					<van-col span="17">{{order.order_sn}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">押金类型</van-col>
					<van-col span="17">{{order.category.title}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">押金金额</van-col>
					<van-col span="17">{{Lang.dollarSign}}{{order.final_fee}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">押金备注</van-col>
					<van-col span="17">{{order.note}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">付款时间</van-col>
					<van-col span="17">{{order.paytime_cn}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">付款方式</van-col>
					<van-col span="17">{{order.pay.text}}</van-col>
				</van-row>
			</div>
			<div class="bg-default margin-10 padding-10 font-14" v-if="order.to_deliveryer.id > 0">
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">退押金配送员</van-col>
					<van-col span="17">{{order.to_deliveryer.title}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">手机号</van-col>
					<van-col span="17">{{order.to_deliveryer.mobile}}</van-col>
				</van-row>
			</div>
			<div class="bg-default margin-10 padding-10 font-14" v-if="order.from_deliveryer.id > 0">
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">收押金配送员</van-col>
					<van-col span="17">{{order.from_deliveryer.title}}</van-col>
				</van-row>
				<van-row class="margin-10-tb">
					<van-col span="7" class="c-gray">手机号</van-col>
					<van-col span="17">{{order.from_deliveryer.mobile}}</van-col>
				</van-row>
			</div>
		</div>
		<van-popup v-model="popupStatus" position="bottom">
			<div class="popup-title">
				<span>订单跟踪</span>
				<span class="c-info font-14" @click="onTogglePopup">关闭</span>
			</div>
			<div class="popup-content">
				<van-steps direction="vertical" :active="order.logs_length - 1" active-color="#44BB00">
					<van-step class="border-0px" v-for="(item, index) in order.logs" :key="index">
						<div class="flex-lr">
							<span>{{item.title}}</span>
							<span>{{item.addtime_cn}}</span>
						</div>
						<div class="margin-5-t">{{item.note}}</div>
					</van-step>
				</van-steps>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import QRcode from 'qrcodejs2';
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				popupStatus: false,
				id: 0,
				order: {
					category: {},
					from_deliveryer: {},
					to_deliveryer: {},
					status_cn: {},
					status: '',
					logs: {},
					last_log: {},
					pay: {},
					refund: {},
				},
				qrcode: ''
			}
		},
		methods: {
			onTogglePopup() {
				this.popupStatus = !this.popupStatus;
			},

			onUpdateOrderStatus(id, type) {
				var params = {
					url: 'wmall/mall/deposit/order_update',
					data: {
						id: id,
						type: type,
					}
				};
				this.util.jspost(params);
			},

			onLoad() {
				var that = this;
				this.util.request({
					url: 'wmall/mall/deposit/order_detail',
					data: {
						id: this.id,
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					result = result.message;
					this.order = result.order;
					if(this.order.status == 2) {
						this.qrcode = '';
						this.$nextTick(function() {
							this.newQrcode(this.order.refund_qrcode);
						});
					}
				})
			},
			newQrcode(text) {
				let qrcode = new QRcode('qrcode', {
					width: 150,
					height: 150, // 高度
					text: text, // 二维码内容
					image: ''
				});
			},

			onQueryRefundStatus() {
				var that = this;
				that.util.request({
					url: 'wmall/mall/deposit/order_detail',
					data: {
						id: this.id,
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					result = result.message;
					this.order = result.order;
					that.util.$toast(this.order.refund.text, 'refresh');
					return false;
				});
			},

		},
		mounted() {
			this.onLoad();
		},
		created() {
			this.querys = this.util.parseQuery(this.$route.query);
			if(this.querys && this.querys.id) {
				this.id = this.querys.id;
			}
		},
	}
</script>

<style>
	#deposit-order-detail .order-status{
		border-bottom: 1px solid #fff;
	}
	#deposit-order-detail .order-status .order-btn{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
	}
	#deposit-order-detail .order-status .order-btn .btn{
		font-size: 14px;
		padding: 8px 10px;
		border: 1px solid #ddd;
		border-radius:3px;
		margin: 0 5px;
	}

	/*底部弹出层*/
	#deposit-order-detail .van-popup .popup-title{
		height: 44px;
		padding: 0 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		background-color: #f5f5f5;
	}
	#deposit-order-detail .van-popup .popup-content{
		min-height: 200px;
		max-height: 400px;
		overflow-x: hidden;
		overflow-y: scroll;
		background-color: #f5f5f5;
	}
	#deposit-order-detail .pay-code{
		width: 100%;
		color: #333;
		padding: 15px;
		background-color: #fff;
	}
	#deposit-order-detail .pay-code .qrcode{
		width: 150px;
		font-size: 0px;
		margin: 0 auto;
	}
	#deposit-order-detail .pay-code .code-text{
		color: #999;
		margin-top: 10px;
		font-size: 14px;
		text-align: center;
	}
	#deposit-order-detail .pay-code .wrap-button{
		margin-top: 10px;
		text-align: center;
	}

</style>


